import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { Link } from 'react-router-dom'

const MeetupDetails = props => {
  const [details, setDetails] = useState({})
  let isMounted
  useEffect(() => {
    isMounted = true
    axios
      .get(`http://localhost:3000/api/meetups/${props.match.params.id}`)
      .then(response => {
        if (isMounted) {
          setDetails(response.data)
        }
      })
      .catch(err => console.log(err))

    return () => {
      let controller = new AbortController()
      controller.abort()
      isMounted = false
    }
  }, [])

  const DeleteAction = () => {
    axios
      .delete(`http://localhost:3000/api/meetups/${details.id}`)
      .then(res => {
        props.history.push('/')
      })
      .catch(err => console.log(err))
  }

  return (
    <div>
      <br />
      <Link className='btn grey' to='/'>
        Back
      </Link>
      <h1>{details.name}</h1>
      <ul className='collection'>
        <li className='collection-item'>city : {details.city}</li>
        <li className='collection-item'>Address: {details.address}</li>
      </ul>
      <Link className='btn' to={`/meetups/add/${details.id}`}>
        Edit{' '}
      </Link>
      <button className='btn red right' onClick={DeleteAction}>
        Delete
      </button>
    </div>
  )
}

export default MeetupDetails
