import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { Link } from 'react-router-dom'

const EditMeetup = props => {
  const [name, setName] = useState('')
  const [city, setCity] = useState('')
  const [address, setAddress] = useState('')
  const [isSubmitted, setIsSubmitted] = useState(false)
  const [isEdited, setIsEdited] = useState(false)

  let isMounted
  useEffect(
    () => {
      isMounted = true
      if (isSubmitted) {
        axios
          .request({
            method: 'post',
            url: `http://localhost:3000/api/meetups`,
            data: { name, city, address }
          })
          .then(response => {
            if (isMounted) {
              props.history.push('/')
            }
          })
          .catch(err => console.log(err))
      }
      if (isEdited) {
        axios
          .request({
            method: 'put',
            url: `http://localhost:3000/api/meetups/${props.match.params.id}`,
            data: { name, city, address }
          })
          .then(response => {
            if (isMounted) {
              props.history.push('/')
            }
          })
          .catch(err => console.log(err))
      }
      if (props.match.params.id) {
        axios
          .get(`http://localhost:3000/api/meetups/${props.match.params.id}`)
          .then(response => {
            if (isMounted) {
              setName(response.data.name)
              setCity(response.data.city)
              setAddress(response.data.address)
            }
          })
          .catch(err => console.log(err))
      }

      return () => {
        isMounted = false
      }
    },
    [isSubmitted, isEdited]
  )

  const SubmitAction = () => {
    if (props.match.params.id) {
      setIsEdited(true)
    } else {
      setIsSubmitted(true)
    }
  }

  return (
    <div>
      <br />
      <Link className='btn grey' to='/'>
        Back
      </Link>

      <form>
        <div className='input-field'>
          <input
            type='text'
            name='name'
            value={name}
            onChange={e => setName(e.target.value)}
          />
          <label htmlFor='name' className={name ? 'active' : ''}>
            Name
          </label>
        </div>

        <div className='input-field'>
          <input
            type='text'
            name='city'
            value={city}
            onChange={e => setCity(e.target.value)}
          />
          <label htmlFor='city' className={city ? 'active' : ''}>
            City
          </label>
        </div>

        <div className='input-field'>
          <input
            type='text'
            name='address'
            value={address}
            onChange={e => setAddress(e.target.value)}
          />
          <label htmlFor='address' className={address ? 'active' : ''}>
            Address
          </label>
        </div>

        <button className='btn' type='button' onClick={SubmitAction}>
          Submit
        </button>
      </form>
    </div>
  )
}

export default EditMeetup
